import { isObject, isString } from '../utils/is';

export class ChatSDKError extends Error {
  name: string = 'ChatSDKError';
  data: unknown;
  additionalInfo?: unknown;

  constructor(error: unknown, data?: unknown) {
    super();

    this.data = data;

    if (error instanceof ChatSDKError) {
      this.message = error.message;
      this.stack = error.stack;
      this.cause = error.cause;
      this.data = error.data;
      this.additionalInfo = data;

      return;
    }

    this.message = `[${this.name}]: ${this._getErrorMessage(error)}`;

    if (error instanceof Error || (isObject(error) && 'stack' in error)) {
      this.stack = error.stack as string;
    }
  }

  private _getErrorMessage(error: unknown): string {
    if (error instanceof Error) {
      return error.message;
    }

    if (typeof error === 'string') {
      return error;
    }

    if (isObject(error) && 'message' in error && isString(error.message)) {
      return error.message;
    }

    return JSON.stringify(error);
  }
}
