import { getBrowserLanguage } from './browserFingerprint/getBrowserFingerprint';
import type { ChatSDKOptions } from './ChatSDKOptions';
import { APP_NAME } from './env/appName';
import { NetworkRequestMetadata } from './request/NetworkRequestMetadata';
import { validateAndFilterHttpHeaders } from './request/validateAndFilterHttpHeaders';
import { validateAndFilterWebSocketQuery } from './request/validateAndFilterWebSocketQuery';
import { getSecureSessionsTypeFromOptions } from './securedSession/getSecuredSessionType';
import { generateId } from './utils/generateId';
import { SDKVariableStorage } from './variables/VariableStorage';

export function initializeSdkVariables(options: ChatSDKOptions): void {
  SDKVariableStorage.AUTHORIZATION_CODE = options.authorizationCode;
  SDKVariableStorage.BRAND_ID = options.brandId;
  SDKVariableStorage.CHANNEL_ID = options.channelId;
  SDKVariableStorage.APP_NAME = options.appName ?? APP_NAME;
  SDKVariableStorage.APP_VERSION = `${options.appVersion ?? 0}`;
  // Store the destinationId, visitId, and visitorId for payload data use
  SDKVariableStorage.DESTINATION = options.destinationId;
  SDKVariableStorage.VISIT_ID = options.visitId;
  SDKVariableStorage.VISITOR_ID = options.visitorId ?? generateId();
  SDKVariableStorage.LANGUAGE = options.language ?? getBrowserLanguage();
  SDKVariableStorage.SECURED_SESSION = getSecureSessionsTypeFromOptions(
    options.securedSession,
  );
  SDKVariableStorage.CLIENT_SIDE_EVENTS_THROTTLING =
    options.isClientSideEventsThrottlingEnabled;

  SDKVariableStorage.REQUEST_METADATA = initializeRequestMetadata(options);
}

export const initializeRequestMetadata = (
  options: ChatSDKOptions,
): NetworkRequestMetadata => {
  const onError = options.onError;

  // validate http headers
  const httpHeaders = validateAndFilterHttpHeaders(
    options.networkRequestMetadata?.httpHeaders,
    onError,
  );

  // validate websocket query params
  const websocketQuery = validateAndFilterWebSocketQuery(
    options.networkRequestMetadata?.websocketQuery,
    onError,
  );

  return {
    httpHeaders,
    websocketQuery,
  };
};
